<template>
	<div id="carry">
		<section>                
      <span class="title">{{$t('mentionMoney.mentionMoney')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm" ref="userForm">
        	<el-form-item :label="$t('coins')" prop="coins">
        		<el-select v-model="userForm.coins" :placeholder="$t('All')">
			      <el-option label="USDG" value="USDG"></el-option>
			      <el-option label="BTY" value="BTY"></el-option>
			    </el-select>
        	</el-form-item>
            <el-form-item :label="$t('SD')" prop="startTime">
                <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm.startTime"></el-date-picker>              
            </el-form-item>
            <el-form-item :label="$t('go')" prop="endTime" >
                <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm.endTime"></el-date-picker>
            </el-form-item>
            <el-form-item  prop="enterpriseName">
                <el-input :placeholder="$t('pleaseEnter')" v-model="input" class="input-with-select">
                  <el-select v-model="select" slot="prepend" :placeholder="$t('Pchoose')">
                     <el-option label="UID" value="uid"></el-option>
                     <el-option :label="$t('phone')" value="phoneNumber"></el-option>
                     <el-option :label="$t('userName')" value="username"></el-option>
                     <el-option :label="$t('email')" value="email"></el-option> 
                  </el-select> 
                </el-input>
            </el-form-item>
            
            <el-form-item>
                 <el-button class="warning" v-on:click="getUsers">{{$t('search')}}</el-button>
            </el-form-item>
            <el-form-item>
                 <el-button class="info" @click="handleReset('userForm')">{{$t('reset')}}</el-button>                 
            </el-form-item>
        </el-form>
      </el-col>

      <el-table :data="userListTop"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
           <el-table-column prop="id"  :label="$t('mentionMoney.tableHead[0]')" width="160">
            </el-table-column>
            <el-table-column prop="uid" :label="$t('mentionMoney.tableHead[1]')" width="160">
            </el-table-column>
            <el-table-column prop="personName"  :label="$t('mentionMoney.tableHead[2]')">
            </el-table-column>
            <el-table-column prop="phoneNumber"  :label="$t('mentionMoney.tableHead[3]')" width="155">
            </el-table-column>
            <el-table-column prop="coinType" :label="$t('mentionMoney.tableHead[4]')">
            </el-table-column>
            <el-table-column prop="coinAmount"  :label="$t('mentionMoney.tableHead[5]')">
            </el-table-column>
            <el-table-column prop="outerAddress" :label="$t('mentionMoney.tableHead[6]')" width="310">
            </el-table-column>
            <el-table-column prop="todayTotal"  :label="$t('mentionMoney.tableHead[7]')" width="130">
            </el-table-column>
            <el-table-column prop="historyNum"  :label="$t('mentionMoney.tableHead[8]')" width="120">
            </el-table-column>
            <el-table-column prop="ctime"  :label="$t('mentionMoney.tableHead[9]')" width="160">
            </el-table-column>
            


            <el-table-column :label="$t('mentionMoney.tableHead[10]')" width="150" >
                <template slot-scope="scope" >
                  <!-- <el-button  v-if="scope.row.applyStatus ==1"
                    size="mini" class="tableBtn"
                    @click="firstPass(scope.$index,scope.row)">{{$t('firstTrial')}}
                  </el-button>
                  <el-button v-if="scope.row.applyStatus ==2"
                    size="mini" class="tableBtn"
                    @click="secondPass(scope.$index,scope.row)">{{$t('reCheck')}}
                  </el-button>
                  <el-button
                    size="mini" class="tableBtn"
                    @click="rejectWithdraw(scope.$index,scope.row)">{{$t('repeal')}}
                  </el-button> -->
                  <el-button  v-if="scope.row.applyStatus ==1"
                    size="mini" class="tableBtn"
                    @click="open2(scope.$index,scope.row,firstPass)">{{$t('firstTrial')}}
                  </el-button>
                  <el-button v-if="scope.row.applyStatus ==2"
                    size="mini" class="tableBtn"
                    @click="open2(scope.$index,scope.row,secondPass)">{{$t('reCheck')}}
                  </el-button>
                  <el-button
                    size="mini" class="tableBtn"
                    @click="open2(scope.$index,scope.row,rejectWithdraw)">{{$t('repeal')}}
                  </el-button>
                  
                </template>
            </el-table-column>


      </el-table>
      <!-- <div class="page" v-show="userListTop.length != 0"> -->
      <div class="page" v-show="total1>10">
      <el-col :span="24" class="toolbar  ">
         <el-pagination
          background
          layout="prev, pager, next"
          :total="total1" @current-change="handleCurrentChange(1)">
        </el-pagination>
      </el-col>
      </div>
   </section>

   <section>
      <span class="title">{{$t('mentionMoney.TBJL')}}</span>
      <el-col :span="24" style="padding-bottom: 0px" class="toolbar">
        <el-form :inline="true" :model="userForm1" ref="userForm1">
        	<el-form-item :label="$t('coins')">
        		<el-select v-model="userForm1.coins" :placeholder="$t('All')">
			      <el-option label="USDG" value="USDG"></el-option>
			      <el-option label="BTY" value="BTY"></el-option>
			    </el-select>
        	</el-form-item>

			    <!-- <el-form-item :label="$t('firstInstance')">
        		<el-select v-model="userForm1.firstInstance" :placeholder="$t('Pchoose')">
			      <el-option label="1" value="1"></el-option>
			      <el-option label="2" value="2"></el-option>
			       </el-select>
    	    </el-form-item>

        	<el-form-item :label="$t('reviewOfficer')">
        		<el-select v-model="userForm1.reviewOfficer" :placeholder="$t('Pchoose')">
			      <el-option label="1" value="1"></el-option>
			      <el-option label="2" value="2"></el-option>
			    </el-select>
        	</el-form-item> -->

          <el-form-item :label="$t('SD')">
              <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm1.startTime"></el-date-picker>              
          </el-form-item>
          <el-form-item :label="$t('go')">
              <el-date-picker type="date" :placeholder="$t('CD')" v-model="userForm1.endTime"></el-date-picker>
          </el-form-item>
          <el-form-item>
              <el-input :placeholder="$t('pleaseEnter')" v-model="input1" class="input-with-select">
                <el-select v-model="select1" slot="prepend" :placeholder="$t('Pchoose')">
                   <el-option label="UID" value="uid"></el-option>
                   <el-option :label="$t('phone')" value="phoneNumber"></el-option>
                   <el-option :label="$t('userName')" value="username"></el-option>
                   <el-option :label="$t('email')" value="email"></el-option> 
                </el-select> 
              </el-input>
          </el-form-item>
            
          <el-form-item>
               <el-button class="warning" v-on:click="queryFrontUserWithdrawHistory">{{$t('search')}}</el-button>
          </el-form-item>
          <el-form-item>
               <el-button class="info" v-on:click="handleReset1('userForm1')">{{$t('reset')}}</el-button>                 
          </el-form-item>
        </el-form>
      </el-col>

      <el-table :data="userList"   v-loading="userLoading" style="width: 100%;" :row-class-name="tableRowClassName" >
           <el-table-column prop="id"  :label="$t('mentionMoney.tableHeadDown[0]')" width="160">
            </el-table-column>
            <el-table-column prop="uid" :label="$t('mentionMoney.tableHeadDown[1]')" width="160">
            </el-table-column>
            <el-table-column prop="personName"  :label="$t('mentionMoney.tableHeadDown[2]')">
            </el-table-column>
            <el-table-column prop="phoneNumber"  :label="$t('mentionMoney.tableHeadDown[3]')" width="155">
            </el-table-column>
            <el-table-column prop="coinType" :label="$t('mentionMoney.tableHeadDown[4]')">
            </el-table-column>
            <el-table-column prop="coinAmount"  :label="$t('mentionMoney.tableHeadDown[5]')">
            </el-table-column>
            <el-table-column prop="outerAddress" :label="$t('mentionMoney.tableHeadDown[6]')" width="310">
            </el-table-column>
            <el-table-column prop="ctime"  :label="$t('mentionMoney.tableHeadDown[7]')" width="160">
            </el-table-column>
            <el-table-column prop="utime"  :label="$t('mentionMoney.tableHeadDown[8]')" width="160">
            </el-table-column>
            <el-table-column prop="applyStatus"  :label="$t('mentionMoney.tableHeadDown[9]')">
            </el-table-column>
            <el-table-column prop="firstCheckName"  :label="$t('mentionMoney.tableHeadDown[10]')">
            </el-table-column>
            <el-table-column prop="secondCheckName"  :label="$t('mentionMoney.tableHeadDown[11]')">
            </el-table-column>
      </el-table>
      <div class="page" v-show="total2>10">
      <el-col :span="24" class="toolbar" v-show="userList.length!=0">
         <el-pagination
          background
          layout="prev, pager, next"
          :total="total2" @current-change="handleCurrentChange">
        </el-pagination>
      </el-col>
      </div>
   </section>
	</div>
   
</template>
   
<script>
import {ajax,dateStr2GmtTimeStamp,getDataYear,dateStrSecond} from '../../common/js/common.js'

export default {
   data(){
     return{
        input:"",
        select:"",
        input1:"",
        select1:"",
        userForm:{
          coins:'',
          coinsTo:"", //给后台穿的
   		   firstInstance:'',
   		   reviewOfficer:'',
         startTime:'',
         endTime:'',
         enterpriseName:''
       },
        userForm1:{
          coins:'',
          coinsTo:"", //给后台穿的
   		   firstInstance:'',
   		   reviewOfficer:'',
         startTime:'',
         endTime:'',
         enterpriseName:''
       },
       startT:"",
       endT:"",
       startT1:"",
       endT1:"",
       userLoading:false,
       userListTop:[],
       userList:[],
       userWithdraw:{
        page:1,
        rowNum:"10",
       },
       userWithdraw1:{
        page:1,
        rowNum:"10",
       },
       total1:0,
       total2:0,
     }
   },
   methods:{
    // 查询前台用户提币的申请信息
    queryFrontUserWithdraw:function(){
      if(this.select != ""){
           this.userForm[this.select] = this.input;
         }
         if(this.userForm.startTime != ""){
          //  this.userForm.startTime = dateStr2GmtTimeStamp(this.userForm.startTime);
          let  startTimeC = dateStr2GmtTimeStamp(this.userForm.startTime);
           // 获取秒的时间戳用于给后台传数据
           this.startT = dateStrSecond(startTimeC)
         }
         if(this.userForm.endTime != ""){
          //  this.userForm.endTime = dateStr2GmtTimeStamp(this.userForm.endTime);
          let endTimeC = dateStr2GmtTimeStamp(this.userForm.endTime);
           this.endT=dateStrSecond(endTimeC)
         }
         if(this.userForm.coins != ""){
          this.userForm.coinsTo = this.userForm.coins =="USDG"?1:2;
        
         }


         let data = {
          page:this.userWithdraw.page,
          rowNum:this.userWithdraw.rowNum,
          uid:"",
          personName:"",
          phoneNumber:"",
          email:"",
          startTime:this.startT,
          endTime:this.endT,
          coinType:this.userForm.coinsTo,
         };
          if(this.select=="uid"){
          data.uid = this.input;
         }else if(this.select=="phoneNumber"){
          data.phoneNumber = this.input;
         }else if(this.select == "personName"){
          data.personName = this.input;
        }else if(this.select=="email"){
          data.email = this.input;
        }
        console.log("待审列表:")
        console.log(data)
      ajax(this,this.ApiConfig.assetManagement.queryFrontUserWithdraw,data,(res) => {
        if(res.code ==200){
          console.log(res)
          this.userListTop = res.data.rows;
          let arr = this.userListTop;
                arr.forEach((item,i,arr) => {
                   arr[i].coinType = arr[i].coinType ==2?"BTY":"USDG";
                   arr[i].ctime =getDataYear('Y-M-D h:m:s',arr[i].ctime);
                   arr[i].utime =getDataYear('Y-M-D h:m:s',arr[i].utime);
                });
          this.total1 = res.data.total;
          this.startT ="";
          this.endT ="";
        }else{
          this.$notify({
            title:"提示",
            message:res.message,
            type:"warning"
          })
          return false;
        }
        
      },"POST")
    },
    // 查询前台用户提币的申请历史信息
    queryFrontUserWithdrawHistory:function(){
       if(this.select1 != ""){
           this.userForm1[this.select1] = this.input1;
         }
         if(this.userForm1.startTime != ""){
          //  this.userForm.startTime = dateStr2GmtTimeStamp(this.userForm.startTime);
          let  startTimeC = dateStr2GmtTimeStamp(this.userForm1.startTime);
           // 获取秒的时间戳用于给后台传数据
           this.startT1 = dateStrSecond(startTimeC)
         }
         if(this.userForm1.endTime != ""){
          //  this.userForm.endTime = dateStr2GmtTimeStamp(this.userForm.endTime);
          let endTimeC = dateStr2GmtTimeStamp(this.userForm1.endTime);
           this.endT1=dateStrSecond(endTimeC)
         }
         if(this.userForm1.coins != ""){
          this.userForm1.coinsTo = this.userForm1.coins =="USDG"?1:2;
        
         }
      let data= {
        page:this.userWithdraw1.page,
        rowNum:this.userWithdraw1.rowNum,
        coinType:this.userForm1.coinsTo,
        uid:"",
        personName:"",
        phoneNumber:"",
        email:"",
        // firstChecker:"",
        // secondChecker:"",
        startTime:this.startT1,
        endTime:this.endT1,
      }
      if(this.select1=="uid"){
          data.uid = this.input1;
         }else if(this.select1=="phoneNumber"){
          data.phoneNumber = this.input1;
         }else if(this.select1 == "personName"){
          data.personName = this.input1;
        }else if(this.select1=="email"){
          data.email = this.input1;
        }
        console.log("请求历史记录:",data)
      ajax(this,this.ApiConfig.assetManagement.queryFrontUserWithdrawHistory,data,(res) => {
         console.log('提币记录历史',res)
            console.log(res)
        if(res.code == 200){
         
            this.userList= res.data.rows;
            let arr = this.userList;
                arr.forEach((item,i,arr) => {
                   arr[i].coinType = arr[i].coinType ==1?"USDG":"BTY";
                   arr[i].ctime =getDataYear('Y-M-D h:m:s',arr[i].ctime);
                   arr[i].utime =getDataYear('Y-M-D h:m:s',arr[i].utime);


                  /* if(arr[i].applyStatus==1){
                    arr[i].applyStatus = "申请中"
                   }else if(arr[i].applyStatus==2){
                    arr[i].applyStatus = "初审同意"
                   }else if(arr[i].applyStatus==3){
                    arr[i].applyStatus = "复审通过"
                   }else if(arr[i].applyStatus==4){
                    arr[i].applyStatus = "拒绝"
                   }*/
                   switch(arr[i].applyStatus){
                    case 1:
                      arr[i].applyStatus = "申请中";
                      break;
                    case 2:
                      arr[i].applyStatus = "初审同意";
                      break;
                    case 3:
                      arr[i].applyStatus = "复审通过";
                      break;
                    case 4:
                      arr[i].applyStatus = "拒绝";
                      break;
                   }
                });
              this.total2 = res.data.total;
              this.startT1 ="";
              this.endT1 = "";

        }else{
          this.$notify({
            title:"提示",
            message:res.message,
            type:'warning'
          })
          return false;
        }

       
      },"POST")
    },
   
     open2:function(index,row,fn){
        this.$confirm('此操作将不可逆, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$message({
            type: 'success',
            message: '成功!',
            callback:fn(index,row)
          });
        
        
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });
      },
    // 初审同意前台用户的提币申请
    firstPass:function(index,row){
      let data = {
        id:row.id
      };
      ajax(this,this.ApiConfig.assetManagement.firstPass,data,(res) =>{
        if(res.code==200){
          // console.log(res)
          this.queryFrontUserWithdraw();
        }else{
          thi.$notify({
            title:"提示",
            message:res.message,
            type:"warning"
          })
          return false;
        }
        
      },"POST")
    },
    //复审同意前台用户的提币申请
    secondPass:function(index,row){
        let data = {
        id:row.id
        };
        ajax(this,this.ApiConfig.assetManagement.secondPass,data,(res) =>{
          if(res.code==200){
            // console.log(res)
            this.queryFrontUserWithdraw();
            this.queryFrontUserWithdrawHistory();
          }else{
            this.$notify({
                      title:"提示",
                      message:res.message,
                      type:"warning"
                   })
                   return false;
          }
         
        },"POST")
    },
    //拒绝前台用户的提币申请
    rejectWithdraw:function(index,row){
        let data = {
        id:row.id
        };
        ajax(this,this.ApiConfig.assetManagement.rejectWithdraw,data,(res) =>{
                  if(res.code==200){
                    // console.log(res)
                    this.queryFrontUserWithdraw();
                    this.queryFrontUserWithdrawHistory();
                  }else{
                    this.$notify({
                              title:"提示",
                              message:res.message,
                              type:"warning"
                           })
                           return false;
                        }
         },"POST")
    },
     getUsers:function(){
          this.queryFrontUserWithdraw();
     },
     handleReset:function(forName){
      this.$refs[forName].resetFields();
      this.input ="";
      this.select ="";
      this.userForm.coins="";
      this.userForm.coinsTo="";
      this.userForm.startTime="";
      this.userForm.endTime="";
      this.queryFrontUserWithdraw();
     },
     handleReset1:function(forName){
      this.$refs[forName].resetFields();
      this.input1 ="";
      this.select1 ="";
      this.userForm1.coins="";
      this.userForm1.coinsTo="";
      this.userForm1.startTime="";
      this.userForm1.endTime="";
      this.queryFrontUserWithdrawHistory();
     },
     tableRowClassName:function({row, rowIndex}){
         if(rowIndex % 2 == 1){
            return "success-row"
         }
     },
      handleCurrentChange:function(val,num){
        if(num === 1){
          this.userWithdraw.page = val;
          this.queryFrontUserWithdraw();

        }else{
          console.log("下分页")
          console.log(val)
          this.userWithdraw1.page = val;
          this.queryFrontUserWithdrawHistory();
        }
       
       
     },
   },
   mounted:function(){
    this.queryFrontUserWithdraw();
    this.queryFrontUserWithdrawHistory();
   }
}
</script>

<style scoped lang="postcss">
  .all{
    width:117px;
  }
  .tableBtn{
        padding: 0!important;
    }
    .smallFont{
      color: red;
    }
</style>
<style>
  #carry{
     .el-select .el-input {
     width: 105px;
    }
    
  
  }
</style>





